/* Footer */
.Footer {
	background-color: #b2d6ff;
	order: 3;
	height: 150px;
	display: flex;
	justify-content: space-around;
	flex-direction: column;
}

.socialicons {
	margin-top: 10px;
	display: flex;
}

.socialicons a {
	color: #000000;
	padding: 0 10px;
}

.copyright {
	display: flex;
	margin-bottom: 10px;
}

.copyright p {
	display: flex;
	margin: 0 10px;
	text-align: center;
}
